
import {
  defineComponent,
  onMounted,
  ref,
  computed
} from 'vue'

export default defineComponent({
  name: 'LLRunBtn',
  props: {
    RunText: {
      type: String,
      default: 'Run'
    },
    IngText: {
      type: String,
      default: 'Ing'
    },

    active: {
      type: Boolean,
      default: false
    }
  },
  setup (props, context) {
    const runbtn = ref()
    const btnWidth = ref(0)
    const btnStyle = computed(() => {
      if (props.active) {
        return {
          '--b': 'var(--background-active)',
          '--default-x': '100%',
          '--active-x': '-12%',
          '--running-x': `${btnWidth.value - 10}px`
        }
      } else {
        return ''
      }
    })

    onMounted((): void => {
      btnWidth.value = runbtn.value.clientWidth
    })

    const handleClick = () => {
      context.emit('click')
    }

    return {
      runbtn,
      btnStyle,
      handleClick
    }
  }
})
